


















































































import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {}
})
export default class HomeComponent extends Vue {
  alertText() {
    return (
      'Sabia que a visita regular ao dentista é uma regra de ouro para manter uma boa saúde oral?<p><p>' +
      '<strong>Marque já a sua avaliação médica dentária sem qualquer compromisso!</strong>'
    );
  }
  appTitle() {
    return 'CLÍNICA DE MEDICINA DENTÁRIA DO MIRATEJO';
  }
  slogan() {
    if (this.$store.getters.isPortuguese) {
      return 'A sua <strong>SAÚDE ORAL</strong> é a nossa prioridade!';
    } else {
      return 'Your <strong>ORAL HEALTH</strong> is our priority!';
    }
  }
  alignment = 'end';
  position = 'center';
}
