




































































import { Component, Vue } from 'vue-property-decorator';
import TopBar from '@/components/TopBar.vue';
import FooterComponent from '@/components/FooterComponent.vue';
import FabButton from '@/components/FabButton.vue';
import ContactDialog from '@/components/ContactDialog.vue';
import Home from '@/views/Home.vue';
@Component({
  components: {
    Home,
    FabButton,
    FooterComponent,
    'top-bar': TopBar,
    'contact-dialog': ContactDialog
  }
})
export default class App extends Vue {
  dialog = false;
  isMessage = false;
  isOralEval = false;
  successSnackbar = false;
  errorSnackbar = false;
  invalidSnackbar = false;

  onMessageUs() {
    this.isMessage = true;
    this.dialog = true;
    this.isOralEval = false;
  }
  onAppointment() {
    this.isMessage = false;
    this.dialog = true;
    this.isOralEval = false;
  }

  onAppointmentOralEval() {
    this.isMessage = false;
    this.dialog = true;
    this.isOralEval = true;
  }

  successNotification() {
    return 'A sua mensagem foi enviada e será respondida nas próximas 48 horas. Obrigado pela preferência.';
  }

  errorNotification() {
    return 'Mensagem não enviada. Tente novamente.';
  }

  emailInvalidNotification() {
    return 'Email não é válido.';
  }

  onSuccess() {
    this.successSnackbar = true;
  }

  onError() {
    this.errorSnackbar = true;
  }

  onEmailInvalid() {
    this.invalidSnackbar = true;
  }
  onCloseDialog() {
    this.dialog = false;
  }
}
