






































































































































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';

@Component({})
export default class TopBar extends Vue {
  drawer = false;

  language() {
    if (this.$store.getters.isEnglish) {
      return 'LANGUAGE';
    } else {
      return 'IDIOMA';
    }
  }

  home() {
    if (this.$store.getters.isEnglish) {
      return 'HOME';
    } else {
      return 'INÍCIO';
    }
  }

  aboutUs() {
    if (this.$store.getters.isEnglish) {
      return 'ABOUT US';
    } else {
      return 'SOBRE NÓS';
    }
  }

  clinic() {
    if (this.$store.getters.isEnglish) {
      return 'Clinic';
    } else {
      return 'Clínica';
    }
  }

  team() {
    if (this.$store.getters.isEnglish) {
      return 'Team';
    } else {
      return 'Equipa';
    }
  }

  treatments() {
    if (this.$store.getters.isEnglish) {
      return 'TREATMENTS';
    } else {
      return 'TRATAMENTOS';
    }
  }

  implant() {
    if (this.$store.getters.isEnglish) {
      return 'Oral Surgery & Dental Implants ';
    } else {
      return 'Cirurgia Oral e Implantologia';
    }
  }

  dentistry() {
    if (this.$store.getters.isEnglish) {
      return 'Dentistry & Endodontics';
    } else {
      return 'Endodontia e Dentisteria';
    }
  }

  pediatrics() {
    if (this.$store.getters.isEnglish) {
      return 'Pediatric Dentistry';
    } else {
      return 'Odontopediatria';
    }
  }

  orthodontics() {
    if (this.$store.getters.isEnglish) {
      return 'Orthodontics';
    } else {
      return 'Ortodontia';
    }
  }

  checkup() {
    if (this.$store.getters.isEnglish) {
      return 'Routine Dental Exam & Checkup';
    } else {
      return 'Higiene Oral';
    }
  }

  contacts() {
    if (this.$store.getters.isEnglish) {
      return 'CONTACTS';
    } else {
      return 'CONTACTOS';
    }
  }

  contactUs() {
    if (this.$store.getters.isEnglish) {
      return 'CONTACT US';
    } else {
      return 'CONTACTE-NOS';
    }
  }

  appointment() {
    if (this.$store.getters.isEnglish) {
      return 'Make an Appointment';
    } else {
      return 'Agendamento de Consulta';
    }
  }

  messageUs() {
    if (this.$store.getters.isEnglish) {
      return 'Message Us';
    } else {
      return 'Fale Connosco';
    }
  }

  pagePosition(pos: number) {
    this.$vuetify.goTo(pos);
  }

  onImplant() {
    this.pagePosition(0);
    this.$store.dispatch('setTreatmentSlide', 0);
  }

  onDentistry() {
    this.pagePosition(0);
    this.$store.dispatch('setTreatmentSlide', 1);
  }

  onPediatric() {
    this.pagePosition(0);
    this.$store.dispatch('setTreatmentSlide', 2);
  }

  onOrthodontics() {
    this.pagePosition(0);
    this.$store.dispatch('setTreatmentSlide', 3);
  }

  onCheckup() {
    this.pagePosition(0);
    this.$store.dispatch('setTreatmentSlide', 4);
  }
}
