import axios from 'axios';

export function emailValidator(email: string) {
  const EMAIL_VALIDATOR_KEY =
    'ZbzzAguFhx8E0GPp1vyLyDQHqQIpUV3t17YIhxVok5Klh3zFIh';
  const abstractKey = 'b149be3fb65e4d5a93690bc83fad9b9e';
  const url = `https://app.verify-email.org/api/v1/${EMAIL_VALIDATOR_KEY}/verify/${email}`;
  const urlTwo = `https://emailvalidation.abstractapi.com/v1/?api_key=${abstractKey}&email=${email}`;
  const httpsClient = axios.create();
  httpsClient.defaults.headers.post['Content-Type'] = 'application/json';
  return httpsClient
    .get(urlTwo)
    .then(response => response)
    .catch(error => error);
}
